const defaultStatus = {
  export: false,
  import: false,
  print: false
}

export const popUpStatusReducer = (state = defaultStatus, action) => {

  switch (action.type) {
    case 'change-popUp-status':
      return {
        ...state,
        export: action.export,
        import: action.import,
        print: action.printValue,
      }

    default:
      return state;
  }
};