import {createStore} from 'redux';
import {getAllData} from "../components/api/api";

const defaultData = [];

const a = [];

const b = async () => {
  await getAllData().then(res => res.forEach(item => a.push(item)))
}

b();

export const productDataReducer = (state = a, action) => {

  switch (action.type) {
    case 'add__products':
      return [
        ...action.data,
      ]

    case 'get__default':
      return [...a]

    default:
      return state;
  }
};
