import React, {useContext, useEffect, useReducer, useRef, useState} from "react";
import {ReactComponent as Close} from "./img/closeIcon.svg";
import './styles/ExportData.scss'
import {useDispatch, useSelector} from "react-redux";

import html2canvas from 'html2canvas';
import {jsPDF} from 'jspdf';

import * as XLSX from 'xlsx';
import {Data} from "../../Info/Types/Data";

export const ExportData: React.FC = () => {
  const data: any = useSelector(state => state);
  const dispatch = useDispatch();

  const [fileNameValue, setFileNameValue] = useState('');
  const [exportData] = useState<any []>(data.productDataReducer.map((item: Data) => {
    return {
      'Наименование': item.name,
      'Артикул': item.vendorCode,
      'Категория': item.category,
      'Тип': item.type,
      'Ед.измерения': item.unitType,
      'Цена.закупки': item.perchasePrice,
      'Цена продажи': item.salePrice,
      'Статус': item.status,
    }
  }));
  const [radioAValue, setRadioAValue] = useState(false);
  const [radioBValue, setRadioBValue] = useState(false);

  // const handleDownloadPdf = async () => {
  //   const element = printRef.current;
  //   const canvas = await html2canvas(element);
  //   const data = canvas.toDataURL('image/png');
  //
  //   const pdf = new jsPDF();
  //   const imgProperties = pdf.getImageProperties(data);
  //   const pdfWidth = pdf.internal.pageSize.getWidth();
  //   const pdfHeight =
  //     (imgProperties.height * pdfWidth) / imgProperties.width;
  //
  //   pdf.addImage(data, 'PNG', 0, 0, pdfWidth, pdfHeight);
  //   pdf.save('print.pdf');
  // };

  console.log(data.productDataReducer)

  const handleOnExport = () => {
    const wb = XLSX.utils.book_new();
    const ws = XLSX.utils.json_to_sheet(exportData);

    XLSX.utils.book_append_sheet(wb, ws, 'test');

    XLSX.writeFile(wb, `${fileNameValue}.xlsx`);
  }

  const handleOnInput = (e: React.FormEvent<HTMLInputElement>) => {
    setFileNameValue(e.currentTarget.value);
  }

  const handleOnCloseExport = () => {
    dispatch({type: 'change-popUp-status', export: false, printValue: false, import: false})
  }

  const handleOnRadio = (text?: string) => {
    if (text === 'a') {
      setRadioAValue(true)
      setRadioBValue(false)
      return;
    }

    setRadioAValue(false)
    setRadioBValue(true)
  }

  const popUpRef = useRef(null);

  useEffect(() => {
    // if (exportData.length === 0) {
    //   const newData =
    //
    //     setExportData(newData);
    // }

    const handleOnClick = (e: any) => {
      if (e.target.className !== 'export-data') return;
      handleOnCloseExport();
    }

    document.addEventListener('click', handleOnClick, true);

    return () => {
      document.removeEventListener('click', handleOnClick, true);
    }
  }, [popUpRef])

  return (
    <>
      <div ref={popUpRef} className={'export-data'}>
        <div className={'export-data__container'}>
          <Close onClick={handleOnCloseExport} className={'export-data--close '}/>
          <div className={'export-data__body'}>
            <h5 className={'export-data__body-title'}>
              Экспорт данных
            </h5>
            <p className={'export-data__body-counter'}>
              Позиций для экспорта: <span className={'export-data__body-counter--num'}>{exportData.length}</span>
            </p>
            <form className={'export-data__form'}>
              <input
                value={fileNameValue}
                onInput={handleOnInput}
                placeholder={'Имя файла'}
                className={'export-data__form-input'}
                type="text"
              />

              <div>
                <label htmlFor="file-name-A">
                  <input onChange={() => handleOnRadio('a')} id={'file-name-A'} name={'file-name'} type="radio"/>
                  PDF
                </label>

                <label htmlFor="file-name-B">
                  <input onChange={() => handleOnRadio()} id={'file-name-B'} name={'file-name'} type="radio"/>
                  XLS
                </label>
              </div>
            </form>
          </div>
          <button onClick={() => {
            if (radioAValue) {
              console.log(123)
              return;
            }

            handleOnExport();
          }} className={'export-data__button'} type={"button"}>
            Экспорт
          </button>
        </div>
      </div>
    </>
  )
}